import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';
import { ResultPage } from '../result/result';

@Component({
  selector: 'page-mysims',
  templateUrl: 'mysims.html',
})
export class MysimsPage {

  cards : any = [];
  me : any = '';
  instr : any = '';
  constructor(public navCtrl: NavController, public navParams: NavParams, private http : HttpClient) {
     this.cards=[];
  }

  ionViewDidLoad() {
    this.cards=[];
    this.instr='';
    this.me=window.localStorage.getItem('uniq');
    let that=this;
       let url = 'https://mysmilesim.dental/server/mysims.php?me='+window.localStorage.getItem('uniq')+'&end='+Math.random();
        this.http.get(url).subscribe(
         data => {
      	let s = JSON.stringify(data);
                let d = JSON.parse(s);
       that.cards = d.cards;
            that.instr = d.instr;
         },
         err => {
         }
        ); 

  }

 goPic(i)
 { this.navCtrl.push( ResultPage, { id : i});
 }

}



